<template>
  <div class="character">
    <img :src="character.image" :alt="character.name" />
    <div class="character__info">
      <h3>{{ character.name }}</h3>
      <div class="status">
        <span
          :class="
            character.status == 'Alive'
              ? 'alive'
              : character.status == 'Dead'
              ? 'dead'
              : 'default'
          "
        ></span>
        <span>{{ character.status }} - {{ character.species }}</span>
      </div>
      <div class="origen">
        <span>
          Origin:
          {{ character.origin.name }}
        </span>
      </div>
      <div class="location">
        <span>
          Location:
          {{ character.location.name }}
        </span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["character"],
};
</script>

<style lang="scss">
.character {
  background-color: var(--background-card);
  border-radius: 20px;
  border: 1px solid white;
  box-shadow: 0 0 10px 1px var(--background-body);
  overflow: hidden;
  cursor: pointer;
  transition: transform 200ms ease-in-out;
  height: 100%;
  &:hover {
    transform: scale(1.05);
    border: 1px solid var(--text-orange);
    h3 {
      color: var(--text-orange);
    }
  }
  span {
    color: var(--text-gray);
    padding-left: 1rem;
  }
  h3 {
    margin-bottom: 0.5rem;
    text-align: center;
  }
  &__info {
    padding: 1.5rem;
    .status {
      display: flex;
      align-items: center;
      margin-bottom: 0.5rem;
      span {
        color: var(--text-gray);
        &:first-child {
          width: 10px;
          height: 10px;
          border-radius: 50%;
          margin-right: 0.5rem;
        }
      }
      .alive {
        background-color: green;
      }
      .dead {
        background-color: red;
      }
      .default {
        background-color: white;
      }
    }
    .origin {
      margin-bottom: 0.5rem;
    }
  }
  .location {
    font-size: 1rem;
  }
}
</style>
